<template>
    <div class="register">
        <!-- <button @click="SocialLogin('Google')">Google 登入</button> -->
    </div>

</template>

<script>
import * as types from '../plugins/helper/mutation-types';
/* eslint-disable no-console */
export default {
  name: 'register',
  data() {
    return {
      registerForms: {
        name: '',
        email: '',
        password: '',
        password_confirmation: '',
        mobile: '',
        platform: '',
        station: '',
      },
    };
  },
  methods: {
    register() {
      this.axios({
        method: 'post',
        baseURL: types.BASE_URL,
        url: '/api/register',
        data: this.registerForms,
      }).then((response) => {
        console.log(response.data);
      }).catch((error) => {
        console.log(error.response.data);
      });
    },
    SocialLogin(provider) {
      this.axios({
        method: 'get',
        baseURL: types.BASE_URL,
        url: `api/login/${provider}`,
        headers: {
          'X-Content-Type-Options': 'nosniff',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET,PUT,POST,DELETE,PATCH,OPTIONS',
        },
      }).then((response) => {
        console.log(response.data);
        window.document.location.href = response.data;
      }).catch((err) => {
        console.log(err.response.data);
      });
    },
  },
};
</script>
