<template>
    <div class="login">
        <h1>Login component</h1>
    </div>
</template>
<script>
/* eslint-disable no-console */
/* eslint-disable no-undef */
/* eslint-disable no-unused-vars */
import axios from 'axios';
import _ from 'lodash';
import * as types from '../plugins/helper/mutation-types';

export default {
  name: 'login',
  data() {
    return {
      loginForm: {
        email: types.EMAIL1,
        password: types.PASSWORD1,
      },
    };
  },
  created() {
    this.createLoginHttp();
  },
  methods: {
    createLoginHttp() {
      const box = [];
      const http = axios.create({
        baseURL: types.BASE_URL,
      });
      http({
        method: 'post',
        url: types.API_LOGIN,
        data: this.loginForm,
      }).then((response) => {
        const alertPayload = {
          type: 'success',
          value: true,
          dismissible: true,
          transition: 'scale-transition',
          message: response.data.success[0],
        };
        const guard = response.data.tokenData[0];
        guard.Authenticate = true;
        this.$store.dispatch(types.SET_GUARD, guard).then(() => {
          this.$store.commit(types.SET_PROFILE, response.data.userData[0]);
          this.$store.commit(`vuetify/${types.MODIFY_ALERT}`, alertPayload);
          this.$router.push({ name: 'home' });
          setTimeout(() => {
            this.$store.commit(`vuetify/${types.MODIFY_ALERT}`, { value: false, transition: 'scale-transition' });
          }, 5000);
        });
      }).catch((error) => {
        // 1.要先前端驗證,確認沒誤再post
        // 2.這邊要設計彈出視窗,提示錯誤
        _.forEach(error.response.data.errors, (key) => {
          box.push(key);
        });
        const alertPayload = {
          value: true,
          outlined: true,
          type: 'error',
          dismissible: true,
          transition: 'scale-transition',
          message: box.toString(),
        };
        this.$store.dispatch(`vuetify/${types.MODIFY_ALERT}`, alertPayload);
        setTimeout(() => {
          this.$store.dispatch(`vuetify/${types.MODIFY_ALERT}`, { value: false, transition: 'scale-transition' });
        }, 5000);
      });
    },
  },
};
</script>
